.main-header {
    min-width: 100%;
    display: flex;
    justify-content: center;
    z-index: 1;
}

.main-header__logo {
    padding-top: 22px;
    padding-bottom: 22px;
    display: flex;
    justify-content: center;
}

.main-header__logo-image {
    display: block;
    fill: #000000;
}

.main-header__logo-image--black-back {
    display: none;
}

.main-header__logo-image--black-splat {
    display: none;
}

@media (min-width: @tablet-width) {
    .main-header {
        box-shadow: 0px 1px 0px #ECECEC;
    }

    .main-header__logo {
        padding-top: 24px;
        padding-bottom: 20px;
        display: flex;
        justify-content: flex-start;
    }    

    .main-header__logo-image {
        display: none;
    }

    .main-header__logo-image--black-back {
        display: block;
        fill: #FFFFFF;
    }

    .main-header__logo-image--black-splat {
        display: none;
    }
}

@media (min-width: @desktop-width) {
    .main-header__logo {
        padding-top: 9px;
        padding-bottom: 13.5px;
        display: flex;
        justify-content: flex-start;
    }    

    .main-header__logo-image {
        display: none;
    }

    .main-header__logo-image--black-back {
        display: none;
    }

    .main-header__logo-image--black-splat {
        display: block;
        fill: #FFFFFF;
    }
}